/* Card */
.cardHeader {
  color: black;
  width: 400px;
  /* margin: 10px 100px; */
  padding: 15px;
  /* background-image: linear-gradient(270deg, #e32121, #aa1010); */
  border: 1px solid #e61b33;
  overflow: hidden;
  transition: 500ms;
  -webkit-mask-image: linear-gradient(yellow);
}
.cardHeader img {
  height: 250px;
}
.cardHeader .text {
  padding: 10px 0 50px 0;
}
.card span {
  z-index: 1;
}
.card .cardCover {
  height: 200px;
}
.nav-section div .nav-card {
  /* max-width: 00px;
  max-height: 100px; */
  /* background-color: #e61b33; */
  background-image: linear-gradient(270deg, #e32121, #aa1010);
  cursor: pointer;
  color: white;
  transform: skew(-20deg);
}
.nav-section div .nav-card a {
  color: white;
  text-decoration: none;
}
.nav-section div .nav-card img {
  max-width: 150px;
  transform: skew(20deg); /* Kompensasi transformasi skew */
}
/* Card */
.button-spek {
  margin: 10px;
  width: 50%;
  align-items: center;
  border: 1px solid #dbdbdb;
  border-radius: 10px;
  display: inline-flex;
  font-size: 14px;
  font-weight: 500;
  padding: 7px 14px;
  text-decoration: none;
  color: black;
}
.button-spek:hover {
  text-decoration: none;
  transition: all 300ms ease;
  border-color: red;
  color: red;
}
