/* NAVBAR */
nav {
  font-family: "Play", sans-serif;
}
.pt-8 {
  padding-top: 90px;
}
.fixed {
  width: 100%;
  position: fixed;
}
.nav-menu {
  background: rgb(255, 255, 255);
  width: 120px;
  height: 90px;
  -ms-transform: skew(-20deg);
  -webkit-transform: skew(-20deg);
  transform: skew(-20deg);
  text-align: center;
  cursor: pointer;
  color: black;
}
.link-nav {
  padding-top: 30px;
  font-size: large;
  color: rgb(0, 0, 0);
}
.nav-menu:hover {
  background: rgb(163, 163, 255);
  color: aliceblue;
  width: 120px;
  height: 90px;
  -ms-transform: skew(-20deg);
  -webkit-transform: skew(-20deg);
  transform: skew(-20deg);
  text-align: center;
  cursor: pointer;
}
.nav-menu .link-nav:hover {
  padding-top: 30px;
  font-size: large;
  color: rgb(255, 255, 255);
}

.head-place-holder {
  height: 153px;
}

.head-float-div {
  position: fixed;
  top: 0;
  z-index: 40;
  width: 100%;
}

#headernav {
  justify-content: flex-end;
  width: 100%;
  height: 53px;
  background: #161316;
  padding: 0 13%;
}

.exlink-img {
  height: 13px;
  width: 13px;
}

.nobotton {
  background: 0 0;
  border: 0;
}

.pos-rel {
  position: relative;
}

#headernav .head-navbar {
  height: 100%;
  display: flex;
  align-items: center;
}

#headernav .head-li {
  height: 100%;
  display: flex;
  align-items: center;
  transform: skew(-15deg);
  transition: background 0.5s;
}

#headernav .head-li .head-link {
  transform: skew(15deg);
}

#headernav .head-li.active {
  background-color: #21a0e3;
  background-image: linear-gradient(270deg, #21a0e3, #105aaa);
  transition: background 0.5s;
}

.search-input {
  width: 286px;
  height: 30px;
  background: #fff;
  font-size: 12px;
  font-weight: 600;
  text-align: left;
  color: #323232;
  border: 0;
  border-radius: 20px;
  padding-left: 15px;
  padding-right: 30px;
}

#navsearch {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5px;
}

#headernav .head-link {
  font-size: 12px;
  font-family: "Play";
  font-weight: 400;
  text-align: left;
  color: #fff;
}

#headernav .head-link:hover {
  color: #de2622;
}

#menunav {
  height: 100px;
  background: #fff;
  box-shadow: 10px 20px 20px 0 rgba(0, 0, 0, 0.16);
  justify-content: space-between;
  padding: 0 5%;
  z-index: 46;
}

#menunav .menu-ul {
  height: 100%;
}

#honda-logo {
  width: 150px;
  height: 88px;
}

#menunav .menu-link {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-family: "Play", sans-serif;
  font-weight: 700;
  text-align: left;
  color: #8f0606;
  padding-left: 26px;
  padding-right: 26px;
  height: 100%;
}

#menunav .menu-item {
  transform: skew(-15deg);
  transition: background 0.5s;
  border: 0;
  --bs-nav-tabs-border-width: 0;
}

.menu-link-txt {
  transform: skew(15deg);
}

#menunav .menu-item:focus-visible,
#menunav .menu-link:focus-visible,
#menunav .menu-link-txt:focus-visible {
  outline-offset: 0;
  outline: 0;
  border: 0;
}

#menunav .menu-item .menu-link:hover {
  border: 0;
}

#menunav .menu-item:hover {
  background-color: #e32121;
  background-image: linear-gradient(270deg, #e32121, #aa1010);
  transition: background 0.5s;
}

#menunav .menu-item:hover .menu-link {
  color: #fff;
}

#menunav .active {
  background-color: #e32121;
  background-image: linear-gradient(270deg, #e32121, #aa1010);
  transition: background 0.5s;
}

#menunav .active .menu-link {
  color: #fff;
}

#menucol-div {
  position: relative;
  height: 0;
  overflow: inherit;
  width: 100%;
  z-index: 45;
}

.menucol {
  position: absolute;
  top: 0;
  width: 100%;
}

#menucol-div .collapse.show {
  height: 270px;
  background: rgba(230, 230, 230, 0.95);
  display: flex;
}

#menucol-div .collapsing {
  background: rgba(230, 230, 230, 0.95);
}

.menucol-left {
  display: flex;
  height: 100%;
  background: linear-gradient(270deg, #213d98, #041958);
  transform: skew(-15deg);
  width: 50%;
  margin-left: -40px;
}

.menucol-right {
  height: 100%;
  transform: skew(-15deg);
  width: 50%;
  margin-left: -1px;
}

.menucol-right-li {
  transform: skew(15deg);
}

.menucol-title {
  line-height: 62px;
  border: 1px solid transparent;
  font-size: 46px;
  font-family: "Play", sans-serif;
  font-weight: 700;
  text-align: left;
  color: #fff;
  transform: skew(15deg);
  margin-top: 42px;
  margin-left: 32%;
}

.menucol-right-ul {
  margin-top: 45px;
  list-style: none;
}

.menucol-right-li {
  line-height: 22px;
  border: 1px solid transparent;
  font-size: 17px;
  font-family: "Play", sans-serif;
  font-weight: 400;
  text-align: left;
  color: #213d98;
  padding: 6px 0;
}

.menucol-right-a {
  color: #213d98;
  text-decoration: none;
}

.menucol-right-a:hover {
  font-weight: 700;
  color: #213d98;
}

#menucol-div #product.show {
  height: 524px;
}

#product {
  position: relative;
}

.product-icon-row {
  padding: 60px 20% 20px;
  justify-content: space-between;
}

.product-icon-box {
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
  justify-content: flex-end;
}

.product-icon {
  width: 56px;
  height: auto;
  margin-bottom: 8px;
}

.product-icon-pp {
  width: 45px;
}

.product-icon-text {
  line-height: 17px;
  font-size: 13px;
  font-family: "Play", sans-serif;
  font-weight: 700;
  text-align: center;
  color: #003a84;
  margin-bottom: 5px;
}

.product-icon-box.active {
  border-bottom: 4px solid #1c3176;
}

.product-swiper-row {
  padding: 0 15%;
}

.product-swiper-float-a {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.product-swiper {
  position: relative;
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}

.product-swiper-img {
  width: 220px;
  height: 200px;
  margin: 0 auto;
}

.product-swiper-name {
  padding-left: 25px;
  line-height: 20px;
  font-size: 19px;
  font-family: "NotoSans-Regular";
  font-weight: 700;
  text-align: left;
  color: #000;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  min-height: 40px;
  margin-bottom: 5px;
}

.product-swiper-gray {
  padding-left: 25px;
  font-size: 9px;
  font-family: "NotoSans-Regular";
  font-weight: 400;
  text-align: left;
  color: #777;
  margin-bottom: 0;
}

.product-swiper-price {
  padding-left: 25px;
  font-size: 15px;
  font-family: "NotoSans-Regular";
  font-weight: 700;
  text-align: left;
  color: #000;
  margin-bottom: 35px;
}

.product-swiper-background {
  z-index: -1;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 257px;
  background: #fff;
  border-radius: 25px;
  box-shadow: 15px 15px 15px 0 rgba(0, 0, 0, 0.15);
}

.product-swiper-button {
  width: 112px;
  height: 35px;
  background: radial-gradient(#132151, #223e95);
  border-radius: 25px 0 25px 0;
  line-height: 35px;
  font-size: 12px;
  font-family: "NotoSans-Regular";
  font-weight: 600;
  text-align: center;
  color: #fff;
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: pointer;
  z-index: 42;
}

#product-swiper-btn-L {
  width: auto;
  position: absolute;
  bottom: 140px;
  transform: translateY(-50%);
  left: 10%;
  cursor: pointer;
}

#product-swiper-btn-R {
  width: auto;
  position: absolute;
  bottom: 140px;
  transform: translateY(-50%);
  right: 10%;
  cursor: pointer;
}

