    text-decoration: none;
  }

  .head-link-p {
    margin-bottom: 0;
  }

  .head-link-arrow {
    width: 5.67px;
    height: 9.33px;
    margin: 10px;
    transition: 500ms;
  }

  .head-link-external {
    width: 14px;
    height: 14px;
    margin: 0 10px;
  }

  .head-link-arrow.head-link-open {
    transform: rotate(90deg);
  }

  .head-second-ul {
    list-style: none;
    padding-left: 0;
  }

  .head-second-li {
    height: 33px;
  }

  #products-ph .head-second-ul {
    padding-left: 0;
  }

  #products-ph .head-second-li {
