  }

  .head-main-ul {
    margin-bottom: 40px;
  }

  .header-out-row {
    display: flex;
    align-items: center;
    bottom: 20px;
    padding: 0 6%;
  }

  .header-out-icon {
    height: 20px;
    margin: 7px;
  }

  .headph-img {
    width: 100%;
  }

  .headph-img-p {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 13%;
