    font-size: 20px;
  }
}

@media screen and (max-width: 1440px) {
  .product-icon-row {
    padding: 20px 20% 20px;
  }
}

@media screen and (min-width: 1200px) {
  .show-ph-h {
    display: none;
  }
}

#sider-div {
  position: fixed;
  z-index: 50;
  height: 40px;
  width: 40px;
  border-radius: 80px 0 0 80px;
  background: linear-gradient(270deg, #23409b 10%, #092274 90%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  right: -5px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s;
}

#sider-div.sider-show {
  height: 430px;
  width: 80px;
  border-radius: 50px 0 50px 0;
  padding: 45px 0;
  justify-content: space-between;
  right: 20px;
}

.sider-btn:nth-of-type(1) .sider-icon {
  width: 23px;
  height: 24px;
}

.sider-btn:nth-of-type(2) .sider-icon {
  width: 19px;
  height: 30px;
}

.sider-btn:nth-of-type(3) .sider-icon {
  width: 19px;
  height: 27px;
}

.sider-btn:nth-of-type(4) .sider-icon {
  width: 33px;
  height: 28px;
}

.sider-txt {
  font-size: 10px;
  font-family: "Play";
  font-weight: 400;
  text-align: center;
  color: #fff;
  margin-top: 5px;
  margin-bottom: 0;
}

#sider-arrow {
  cursor: pointer;
  transform: rotate(180deg);
}

.sider-btn {
  display: none;
  text-decoration: none;
}

#sider-div.sider-show .sider-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#sider-div.sider-show #sider-arrow {
  transform: rotate(0deg);
}

